* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(243, 227, 133);
  color: #080b85;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-family: "Open Sans", sans-serif;
  min-height: 100vh;
}

.wrapper {
  border: 2px solid #023441;
  border-radius: 8px;
  box-shadow: 2px 2px 2px #023441;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1rem 0 1.5rem;
  max-width: 80vw;
  margin: 5rem auto 0;
  width: 40rem;
}

h1 {
  text-shadow: 3px 5px 2px rgb(255, 209, 5);
  font-size: 2.2rem;
  text-align: center;
  padding: 0;
  margin: 0.2rem auto 2rem;
  /* width: 12rem; */
}

.form-group {
  /* outline: 3px solid red; */
  margin: 0 auto 2.5rem;
  width: 80%;
}

input {
  background-color: rgba(255, 246, 197, 0.973);
  border-width: 1px;
  padding: 0.4rem 0.5rem;
  width: 75%;
}

button {
  background-color: #023441;
  border: 1px;
  color: rgb(243, 227, 133);
  cursor: pointer;
  /* font-size: 15px; */
  font-weight: 600;
  padding: 0.45rem 0.3rem;
  width: 25%;
}

.todo {
  margin: 0 auto;
  width: 80%;
}

.list-item {
  display: flex;
  justify-content: space-between;
  background-color: rgb(255, 196, 0);
  border-radius: 4px;
  color: #023441;
  font-family: "Poppins", sans-serif;
  list-style: none;
  padding: 0.3rem 0.6rem;
  letter-spacing: 0.5px;
  margin-bottom: 0.15rem;
}

.delete-list {
  cursor: pointer;
  font-size: 18px;
}
